#leaderboard {
    display: "flex";
    justify-content: "center";
    text-align: center;
    background-image: linear-gradient(
      rgba(255, 255, 255, 0.4),
      rgba(255, 255, 255, 0.4)
    ),
    url(/images/bg02.jpg);
  background-size: cover;
  background-position: top;
  background-attachment: fixed;
  min-height: 100vh;
  padding: 70px 10px;
  font-family: 'Raleway', sans-serif;
}


#leaderboard .HallTitle {
    margin-bottom: 20px;
    font-family: "Cinzel Decorative", cursive;
    color: black;
    text-shadow: -1px -1px 10px white, 1px -1px 10px white, -1px 1px 10px white,
    1px 1px 10px white;
    text-align: center;
    padding-bottom: 10px;
}


#leaderboard ol {
    list-style: none;
    counter-reset: my-awesome-counter;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
  }


#leaderboard ol a {
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 0px 2px 10px rgba(75, 87, 84, 0.5);
    padding: 10px;
    margin-bottom: 8px;
    border-bottom: 6px solid rgb(118, 169, 49);
    counter-increment: my-awesome-counter;
    display: flex;
    justify-content: center;
    align-items: baseline;
    font-size: 0.8rem;
    min-height: 50px;
    width: 250px;  
    margin: auto;
    margin-bottom: 20px;
    padding-top: 10px;
  }


#leaderboard ol a::before {
    content: counter(my-awesome-counter);
    font-weight: bold;
    font-size: 1.7rem;
    margin-right: 0.5rem;
    font-family: "Cinzel Decorative", cursive;
    line-height: 1;
    padding-left: 10px;
}

