#heroHome {
  background-image: linear-gradient(
      rgba(255, 255, 255, 0.4),
      rgba(255, 255, 255, 0.4)
    ),
    url(/images/bg01.jpg);
  background-size: cover;
  background-position: top;
  background-attachment: fixed;
  min-height: 100vh;
  padding: 70px 10px;
  font-family: 'Raleway', sans-serif;
}

#heroHome .title {
  font-family: "Cinzel Decorative", cursive;
  color: black;
  text-shadow: -1px -1px 10px white, 1px -1px 10px white, -1px 1px 10px white,
  1px 1px 10px white;
  text-align: center;
  padding-bottom: 10px;
}

#heroHome .white-card {
  background-color: rgba(255, 255, 255, 0.7);
  width: 90%;
  margin: auto;
  border-radius: 3px;
  box-shadow: 0px 5px 15px rgb(44, 92, 83);
}


#heroHome .header{
  display: flex;
  justify-content: flex-start;
  margin-bottom: 10px;
}

#heroHome .header h5{
  font-family: "Cinzel Decorative", cursive;
  font-size: 1.5rem;
  padding-top: 10px;
  margin-right: 20px;
  word-wrap: break-word;
  max-width: 150px;
}

#heroHome .header img{
  display: block;
  max-width:150px;
  max-height:95px;
  width: auto;
  height: auto;
}

#heroHome .header .users-info{
  display: flex;
  justify-content: space-between;
  align-items: center;
}


#heroHome .users-info img{
  width: 20px;
  height: auto;
}

#heroHome .header .points-info{
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
}

#heroHome .header .total-points{
  font-size: 0.7rem;
  margin: 0 5px;
}

#heroHome .your-tasks{
  padding: 0px 20px 20px 20px;
}
#heroHome .your-tasks h4{
  font-family: "Neucha", cursive;
}

#heroHome .your-tasks h4 img{
  height: 30px;
}

#heroHome .one-task{
  background-color: rgba(255, 255, 255, 0.6);
  box-shadow: 0px 2px 10px rgba(75, 87, 84, 0.5);
  padding: 10px;
  margin-bottom: 8px;
  border-bottom: 6px solid rgb(118, 169, 49);
  display: flex;
}
#heroHome .date-title{
  width: 90%;
}

#heroHome .date-title p{
  margin-bottom: 0;
}

#heroHome .one-task a{
  color: black;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

#heroHome .one-task p{
  font-size: 0.8rem;
  margin-right: 10px;
  width: 10%;
}

#heroHome .one-task h6{
  text-align: center;
  font-weight: bold;
  margin: 0;
}

#heroHome .one-task .edit-btn{
  display: flex;
  min-width: 50px;
  width: 10%;
}

#heroHome .one-task .edit-btn img{
  width: 20px;
  margin-right: 5px;
}

#heroHome .edit-btn button{
  border:none;
  margin:0;
  padding:0;
  background-color: transparent;
  outline: none;
}

#heroHome .see-all{
  color: black;
  text-decoration: underline;
  padding-bottom: 20px;
  font-weight: bold;
}

#heroHome .see-all a{
  color: black;
  font-weight: bold;
}

#heroHome .btn-add{
  background-color: rgb(118, 169, 49);
  color: white;
  padding: 10px 15px;
  font-size:1rem;
  font-weight: bold;
}

#heroHome .btn-container{
  width: 100%;
  text-align: center;
  margin-top: 20px;
}

#heroHome .carousel-container{
  text-align: center;
  margin-top: 30px;
}

#heroHome .carousel-title{
  font-family: "Cinzel Decorative", cursive;
  color: black;
  text-shadow: -1px -1px 10px white, 1px -1px 10px white, -1px 1px 10px white,
  1px 1px 10px white;
  text-align: center;
  padding-bottom: 10px;
}